.Repos-list {
  font-size: .875em;
  height: 100%;
  list-style: none;
  margin: 0;
  overflow-y: auto;
  padding: 0;
}

.Repo {
  border-radius: .3em;
  border: .0625em solid rgba(0, 0, 0, .1);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  padding: 1em;
  position: relative;
}

.Repo > :last-child {
  margin-bottom: 0;
}

.Repo:not(:last-child) {
  margin-bottom: 1.75em;
}

.Repo:hover,
.Repo:focus {
  background: -webkit-linear-gradient(315deg, transparent 15%, rgba(171, 220, 255, .55) 220%);
  background: -o-linear-gradient(315deg, transparent 15%, rgba(171, 220, 255, .55) 220%);
  background: linear-gradient(135deg, transparent 15%, rgba(171, 220, 255, .55) 220%);
  border-color: rgba(83, 103, 117, .1);
}

.Repo-header {
  -ms-flex-align: center;
      align-items: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  margin-bottom: .5em;
}

.Repo-title {
  color: rgb(3, 150, 255);
  font-size: 1.25em;
  font-weight: 700;
  text-decoration: none;
}

.Repo-title:hover,
.Repo-title:focus {
  text-decoration: underline;
}

.Repo-stats {
  display: -ms-flexbox;
  display: flex;
}

.Repo-stat {
  -ms-flex-align: center;
      align-items: center;
  display: -ms-flexbox;
  display: flex;
  margin: 0;
}

.Repo-stat:not(:last-child) {
  margin-right: 1em;
}

.Repo-stat-icon {
  font-size: inherit;
  margin-right: .25em;
}

.Repo-stat-value {
  font-size: .875em;
}

.Repo-languages {
  border-radius: .3em;
  font-size: .875em;
}



