.Navigation {
  width: 100%;
}

.Navigation-list {
  -ms-flex-align: center;
      align-items: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  list-style: none;
  margin: 0;
  padding: 0;
}

.Navigation-listitem {
  font-size: .875em;

  width: 50%;
}

.Navigation-listitem:not(:last-child) {
  margin-right: 1em;
}

.Navigation-button {
  border: .0625em solid transparent;
  background-color: transparent;
  border-radius: .3em;
  font-size: .875em;
  font-weight: 500;
  padding: .75em 2.25em;
  text-transform: uppercase;
  width: 100%;
}

.Navigation-button:hover,
.Navigation-button:focus,
.Navigation-button:active,
.Navigation-button--active {
  color: #27A8E8;
}

.Navigation-button:active,
.Navigation-button--active {
  font-weight: 600;
}



