.Header {
  -ms-flex-align: center;
      align-items: center;
  background-image: -webkit-linear-gradient(315deg, rgb(171, 220, 255) 0%, rgba(3, 150, 255, .85) 100%);
  background-image: -o-linear-gradient(315deg, rgb(171, 220, 255) 0%, rgba(3, 150, 255, .85) 100%);
  background-image: linear-gradient(135deg, rgb(171, 220, 255) 0%, rgba(3, 150, 255, .85) 100%);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  padding: 1em;
}

@media(min-width: 961px) {
  .Header {
    grid-column: span 2;
  }
}



